.services {
    position: relative;
    padding: 0 10px;
    margin-bottom: 44px;
    &-table {
        display: none;
        transition: all .5s linear;
        margin-bottom: 60px;
        &.active {display: block;}
        &__title {
             font-family: 'PF DinDisplay Pro';
             font-size: 20px;
             font-weight: 900;
             font-stretch: normal;
             font-style: normal;
             line-height: 1.17;
             letter-spacing: 1.3px;
             color: #002337;
             text-transform: uppercase;
             margin-bottom: 22px;
             span {
                 font-family: 'PF Din Text Cond Pro';
                 font-size: 13px;
                 font-weight: 400;
                 font-style: normal;
                 font-stretch: normal;
                 line-height: 2.15;
                 letter-spacing: .41px;
                 text-transform: none;
             }
        }
        &__service {
             position: relative;
             margin-bottom: 15px;
             padding-bottom: 20px;
             border-bottom: solid 1px #e3ddcd;
             &-row {
                  display: flex;
                  p {margin: 0;}
                  &.name {
                      justify-content: space-between;
                      p {
                          display: flex;
                          align-items: center;
                      }
                  }
             }
             &-appointment {
                  margin-left: 20px!important;
                  a {
                      font-size: 0;
                      display: block;
                      width: 16px;
                      height: 16px;
                      background: url("/images/desktop/icons/edit.svg") 50% 50%/contain no-repeat;
                      margin: 0 auto;
                  }
             }
             &-hidden {
                  display: none;
                  margin-top: 7px;
                  p {
                      width: 150px;
                      font-family: 'PF Din Text Cond Pro';
                      font-size: 14px;
                      font-weight: normal;
                      font-stretch: normal;
                      font-style: normal;
                      line-height: 1.29;
                      letter-spacing: 0.34px;
                      &:first-child {color: #7b919e;}
                  }
             }
             &:after {
                  position: absolute;
                  content: '';
                  bottom: -2px;
                  left: calc(50% - 6px);
                  width: 12px;
                  height: 12px;
                  border-right: 1px solid #002337;
                  border-bottom: 1px solid #002337;
                  transform: rotate(45deg);
                  transition: all .3s linear;
             }
             &:before {
                  position: absolute;
                  content: '';
                  bottom: -7px;
                  left: calc(50% - 15px);
                  width: 30px;
                  height: 14px;
                  background-color: #FFFFFF;
                  transition: all .3s linear;
             }
             &.show {
                 &:after {
                      bottom: -10px;
                      transform: rotate(225deg);
                      transition: all .3s linear;
                 }
             }
        }
    }
    &-price {
         position: relative;
         z-index: 50;
    }
