    &-search {
        margin: 5px 10px 30px;
        form {
            position: relative;
            width: 70%;
            input {
                width: 100%;
                height: 31px;
                padding-right: 30px;
                border-radius: 18.5px;
                box-shadow: inset 0 2px 5px 0 #e3ddcd;
                background-color: #f8f6f0;
                outline: none;
                border: none;
                font-style: italic;
                letter-spacing: 0.47px;
            }
            button {
                position: absolute;
                top: 0;
                right: 0px;
                width: 30px;
                height: 31px;
                padding-left: 30px;
                border-radius: 0 18.5px 18.5px 0;
                background-color: #8cde98;
                font-size: 0;
                &:before {
                    position: absolute;
                    content: '';
                    left: calc(50% - 9px);
                    top: calc(50% - 9px);
                    width: 18px;
                    height: 18px;
                    background: url('/images/desktop/icons/search.svg') 50% 50%/contain no-repeat;
                }
            }
        }
    }
